@import "./reset.css";

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  height: 100%;

  background-color: rgb(155, 21, 21);
  padding-top: 100px;
}
.tree {
  font-size: 100px;
  margin-bottom: 30px;

}
.text {
  margin-bottom: 50px;
  font-size: 50px;
  font-family: 'Pushster', cursive;
  
  color: rgb(36, 163, 10);
}
.writer-box {
  width: 350px;
  margin-bottom: 100px;
}
.writer-box p {
  line-height: 35px;
  color: rgba(250, 215, 151, 0.836) ;
  font-size: 20px;
  font-weight: 600;
  font-family: 'Black Han Sans', sans-serif;}

.writer-box p:nth-child(2) {
  margin-top: 30px;
  color: rgb(36, 163, 10) ;
  text-align: right;
}
.box-img {
  width: 300px;
  height: 300px;
  border: solid 7px rgb(26, 117, 3);
  border-radius: 150px;
  box-shadow: 1px 1px 15px 2px rgba(100, 64, 64, 0.7);
  background-position: center;
  background-size: contain;
  background-image: url('../img/box.jpg');
  position: relative;
  animation: pulse-box 2s infinite;
  margin-bottom: 150px;
}
.box-img::before {
  content: "";
  display: block;
  width: 300px;
  height: 300px;
  border-radius: 50%;
  position: absolute;
	animation: pulse 2s infinite;
}

@keyframes pulse-box {
  0% {
		transform: scale(1);
		/* box-shadow: 0 0 0 0 rgba(0, 0, 0, 0.7); */
	}

  20% {
    transform: scale(1.03);
    /* box-shadow: 0 0 0 100px rgba(0, 0, 0, 0); */
  }

  100% {
    transform: scale(1);
    /* box-shadow: 0 0 0 0 rgba(0, 0, 0, 0); */
  }
}
@keyframes pulse {
	0% {
    transform: scale(1.03);
		box-shadow: 0 0 0 0 rgba(214, 0, 0, 0.7);
	}

  70% {
    transform: scale(1);
    box-shadow: 0 0 0 100px rgba(0, 0, 0, 0);
  }

  100% {
    /* transform: scale(0.95); */
    box-shadow: 0 0 0 0 rgba(0, 0, 0, 0);
  }
}